<template>
	<div class="login">
		<!-- breadcrumbs -->
			<div class="breadcrumbs">
				<div class="container">
					<ol class="breadcrumb breadcrumb1 animated wow slideInLeft" data-wow-delay=".5s">
						<li><a href="index.html"><span class="glyphicon glyphicon-home" aria-hidden="true"></span></a></li>
						<li class="active"></li>
					</ol>
				</div>
			</div>
		<!-- //breadcrumbs -->
		
		<!-- login -->
			<div class="login">
					<div class="container">
						<h3 class="animated wow zoomIn" data-wow-delay=".5s"></h3>
						<p class="est animated wow zoomIn" data-wow-delay=".5s">.</p>
						<div class="login-form-grids animated wow slideInUp" data-wow-delay=".5s">
							<form>
								<input type="text" placeholder="" v-model="username" >
								<input type="password" placeholder="" v-model="password" >
								<p>{{mess}}</p>
								<div class="forgot">
									<a href="#">?</a>
								</div>
								<button type="button" value="" @click="login"></button>
							</form>
						</div>
						<h4 class="animated wow slideInUp" data-wow-delay=".5s">For New People</h4>
						<p class="animated wow slideInUp" data-wow-delay=".5s">
							<router-link tag="input" type="button" value="" to="/reg"></router-link>
						()  <a href="##"><span class="glyphicon glyphicon-menu-right" aria-hidden="true"></span></a></p>
					</div>
				</div>
		<!-- //login -->
	</div>
</template>

<script>
	export default {
		name:'login',
		props:{},
		data(){
			return{
				username:'',
				password:'',
				mess:''
			}
		},
		components:{},
		mounted(){},
		updated(){},
		methods:{
			login(){
				this.$axios({
					//
					url:'/api/login',
					method:'post',
					//
					data:{
						username:this.username,
						password:this.password
						
					}
				}).then(
				res=>{
					//
					if(res.err===0){
						//token
						window.localStorage.setItem('user',JSON.stringify(res));
						//user
						if(this.$route.query.backpath){
							this.$router.push({
								path:this.$route.query.backpath
							})
						}else{
							this.$router.push('/home')
						}
						
					}else{
						this.mess=res.msg
					}
				}
				)
			}
		}
	}
</script>

<style>
	/*-- products --*/
	.breadcrumbs{
		padding:1.5em 0;
		background:#f5f5f5;
	}
	.breadcrumb1{
		padding:0;
		margin:0;
	}
	.breadcrumb1 li{
		font-size:1em;
		color:#999;
	}
	.breadcrumb1 li a{
		color:#d8703f;
		text-decoration:none;
	}
	.breadcrumb1 li span {
	    left: 0em;
	    padding-right: 1em;
	    color: #D8703F;
	}
	
	/*  */
	/*-- login --*/
	.login-form-grids{
		width: 45%;
	    padding: 3em;
		background:#F7F7F9;
		margin:3em auto 0;
	}
	.login-form-grids input[type="email"],.login-form-grids input[type="password"],.login-form-grids input[type="text"]{
		outline: none;
	    border: 1px solid #DBDBDB;
	    padding: 10px 10px 10px 45px;
	    font-size: 14px;
	    color: #999;
	    display: block;
	    width: 100%;
	}
	.login-form-grids input[type="email"]{
		background:url(/images/img-sp.png) no-repeat 5px -182px #fff;
	}
	.login-form-grids input[type="password"]{
		background:url(/images/img-sp.png) no-repeat 5px -220px #fff;
		margin:1em 0 0;
	}
	.forgot {
	    margin: 1.5em 0 0;
	}
	.login-form-grids input[type="submit"]{
		outline: none;
	    border: none;
	    padding: 10px 0;
	    font-size: 1em;
	    color: #fff;
	    display: block;
	    width: 100%;
	    background:#FFC229;
	    margin: 1.5em 0 0;
	}
	.login-form-grids input[type="submit"]:hover{
		background:#d8703f;
	}
	.login-form-grids ::-webkit-input-placeholder{
		color:#999;
	}
	.forgot a{
		color:#212121;
		font-size:14px;
		text-decoration:none;
	}
	.forgot a:hover{
		color:#d8703f;
	}
	.login h4{
		margin: 2em 0 0.5em;
	    font-size: 1.5em;
	    color: #212121;
	    text-align: center;
	    text-transform: uppercase;
	}
	.login p{
		font-size:14px;
		color:#999;
		line-height:1.8em;
		margin:0;
		text-align:center;
	}
	.login p a{
		color:#d8703f;
		text-decoration:none;
		font-size: 1.2em;
		padding: 0 .5em;
	}
	.login p a:hover{
		color:#212121;
	}
	.login p a span{
		top: 0.1em;
	    font-size: .7em;
	    left: 0.3em;
	}
</style>
