import Vue from  'vue';
import axios from 'axios';

//router
import router from './router.js';
import vm from '../main.js';


let timer = null;
//https://github.com/axios/axios#interceptors//
//interceptors


//-config:axios{options}axios
axios.interceptors.request.use(function (config) {
	console.log("");
 
	//params
	config.params = config.params || {
		_page:1,
		_limit:10
	}//
	
	
	//token
	let local = window.localStorage.getItem('user'); //string
	//
	// user = user ? JSON.parse(user) : null; //object
	let token = local ? JSON.parse(local).token : '';
	config.headers ={ 
		token
	}
	
	//loading
	//loading
	  clearTimeout(timer)
	  timer = setTimeout(()=>vm.bBLoading=true,500)
	
    return config;//
  }, function (error) {
	  //
    return Promise.reject(error);
  });

//

axios.interceptors.response.use(function (response) {
	console.log("");
	
	//router 
	let path = router.currentRoute.fullPath
	if(response.data.err===2){
		//token
		//,
		router.replace({
			path:'/login',
			query:{
				backpath:path,//
			}
		})
	}
	clearTimeout(timer)
	vm.bBLoading=false;//loading// 
    return response.data;//
	//
    //return response.data;//data


	//
  }, function (error) {
 
    return Promise.reject(error);
  });
  
 
 
 Vue.prototype.$axios = axios; // vm|.$axios
 // Vue.prototype.$http=axios;// vm|.$axios
 // window.axios=axios;//  
 
 //axios
 export default axios;
 